import { ComponentProps, MouseEvent } from "react";
import styles from "./Button.module.scss";
import Icon from "../Icon/Icon";

interface Props extends ComponentProps<"button"> {
  selectedTopic: string | null;
  deselectTopic: () => void;
}

export default function FilterButton({
  selectedTopic,
  deselectTopic,
  ...props
}: Props) {
  const handleClearClick = (event: MouseEvent<HTMLDivElement>) => {
    event.stopPropagation();
    deselectTopic();
  };

  return (
    <div className={styles.filterbutton__wrapper}>
      <button
        type="button"
        className={`${styles.button} ${styles.primary} ${styles.filterbutton} ${
          selectedTopic && styles.active
        }`}
        {...props}
      >
        <div className={styles.filterbutton__content}>
          {selectedTopic ? (
            <div
              onClick={handleClearClick}
              className={`${styles.button} ${styles.deletefilterbutton}`}
            >
              <span className="body-button">{selectedTopic}</span>
              <Icon icon="close" size={12} />
            </div>
          ) : (
            <span>All topics</span>
          )}

          <Icon icon="arrow-drop-down-line" size={24} />
        </div>
      </button>
    </div>
  );
}

