import s from "./QuestionItem.module.scss";
import Icon from "../Icon/Icon";
import Chip from "../Chip/Chip";
import { Question } from "@/models/models";
import Categories from "../Categories/Categories";

type Props = {
  title: string;
  index: number;
  questions?: Question[];
};

export default function QuestionItem({ title, index, questions }: Props) {
  return (
    <div
      key={`questionItem-${index}`}
      id={`question-${index}`}
      className={s.question__items_wrapper}
    >
      <div className={s.question__heading}>
        <Chip title={title} />
      </div>
      <ul className={s.question__items}>
        {questions?.map((question, index) => (
          <li key={`question-${index}`} className={s.question__item}>
            <Icon icon="quote" size={24} />
            <div className={s.question__content}>
              <span className="body">{question.content}</span>
              <Categories subject={question} index={index} />
            </div>
          </li>
        ))}
      </ul>
    </div>
  );
}
