import { Subject } from "@/models/models";
import s from "./Categories.module.scss";
import cx from "clsx";

type Props = {
  subject: Subject;
  index: number;
};

export default function Categories({ subject, index }: Props) {
  return (
    <div className={s.categories}>
      <span className={`${s.category} tag-s`}>
        <div className={cx(s.category__bullet, s.category__bullet_first)}>
          •
        </div>
        {subject.primary}
      </span>
      <span key={`sec1-${index}`} className={`${s.category} tag-s`}>
        <div className={s.category__bullet}>•</div>
        {subject.secondary1}
      </span>
      {subject.secondary2 && (
        <span key={`sec2-${index}`} className={`${s.category} tag-s`}>
          <div className={s.category__bullet}>•</div>
          {subject.secondary2}
        </span>
      )}
    </div>
  );
}
