"use client";

import { useRef, useState } from "react";
import FilterButton from "@/components/Button/FilterButton";
import s from "./Filter.module.scss";
import { FilterOption } from "@/components/Filter/FilterOption";
import Button from "@/components/Button/Button";
import subjects from "@/data/subjects.json";
import questions from "@/data/questions.json";
import Icon from "../Icon/Icon";

interface Props {
  onTopicSelect: (topic: string) => void;
}

export default function Filter({ onTopicSelect }: Props) {
  const ref = useRef<HTMLDialogElement>(null);
  const [selectedTopic, setSelectedTopic] = useState<string | null>(null);
  const [isDialogOpen, setIsDialogOpen] = useState(false);

  const subjectTopics = subjects.flatMap((subject) =>
    [subject.primary, subject.secondary1, subject.secondary2].filter(Boolean)
  );
  const questionsTopics = questions.flatMap((question) =>
    [question.primary, question.secondary1, question.secondary2].filter(Boolean)
  );

  const allTopics = [...subjectTopics, ...questionsTopics];

  const itemCounts: { [key: string]: number } = {};

  for (const item of allTopics) {
    if (item !== null) {
      if (item in itemCounts) {
        itemCounts[item]++;
      } else {
        itemCounts[item] = 1;
      }
    }
  }

  const uniqueTopics = Object.keys(itemCounts);

  return (
    <div className={`${s.filter__wrapper} filter-wrapper`}>
      <dialog ref={ref} className={s.dialog}>
        <div className={s.dialog__element}>
          <header className={s.dialog__header}>
            <h2 className="heading-m">Filter by topic</h2>
            <button
              onClick={() => {
                ref.current?.close();
                setIsDialogOpen(false);
              }}
            >
              <Icon icon="close" />
            </button>
          </header>
          <div className={s.dialog__content}>
            <ul className={s.dialog__list}>
              {uniqueTopics.map((label, index) => (
                <li key={index}>
                  <FilterOption
                    label={label}
                    active={selectedTopic === label}
                    onClick={() => {
                      setSelectedTopic(label);
                      onTopicSelect(label);
                      ref.current?.close();
                      setIsDialogOpen(false);
                    }}
                  />
                </li>
              ))}
            </ul>
          </div>
        </div>
        <Button
          className={s.close_button}
          onClick={() => {
            ref.current?.close();
            setIsDialogOpen(false);
          }}
          icon="close"
          label=""
          rank="primary"
        ></Button>
      </dialog>
      {!isDialogOpen && (
        <FilterButton
          selectedTopic={selectedTopic}
          onClick={(event: React.MouseEvent<HTMLButtonElement>) => {
            event.preventDefault();
            ref.current?.showModal();
            setIsDialogOpen(true);
          }}
          deselectTopic={() => {
            setSelectedTopic(null);
            onTopicSelect("");
          }}
        ></FilterButton>
      )}
    </div>
  );
}
