export default function getStintById(title: string) {
  switch (title) {
    case "diagnostic-encounter":
      return "Diagnostic Encounter";
    case "therapeutic-encounter":
      return "Therapeutic Encounter";
    case "1-week":
      return "1 Week";
    case "1-month":
      return "1 Month";
    case "3-months":
      return "3 Months";
    case "future":
      return "Future (> 3 Months)";
    default:
      return;
  }
}
