import { useEffect, useState } from "react";
import { motion } from "framer-motion";
import cx from "clsx";
import s from "./Timeline.module.scss";
import stints from "@/data/stints.json";
import Icon from "../Icon/Icon";

interface Props {
  isPatientPage: boolean;
}

export default function Timeline({ isPatientPage }: Readonly<Props>) {
  // State to hold the heights of the group and its elements
  const [subjectsHeights, setSubjectsHeights] = useState<number[]>(
    Array(6).fill(0)
  );
  // State to hold the dynamic scaling factors for each timeline element
  const [dynamicScales, setDynamicScales] = useState<number[]>(
    Array(6).fill(0)
  );
  const [scrolled, setScrolled] = useState(false);
  const [isSmallScreen, setIsSmallScreen] = useState<boolean>(
    typeof window !== "undefined" ? window.innerWidth <= 1024 : false
  );

  useEffect(() => {
    const handleResize = () => {
      setIsSmallScreen(window.innerWidth <= 1024);
    };

    if (typeof window !== "undefined") {
      window.addEventListener("resize", handleResize);
      return () => window.removeEventListener("resize", handleResize);
    }
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      if (window.scrollY > 10) {
        setScrolled(true);
      } else {
        setScrolled(false);
      }
    };

    window.addEventListener("scroll", handleScroll);
    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  useEffect(() => {
    // Get references to the relevant DOM elements based on the isPatientPage flag
    const elements = Array.from({ length: 6 }, (_, i) =>
      document.getElementById(isPatientPage ? `question-${i}` : `subjects-${i}`)
    );

    const group = document.getElementById(
      isPatientPage ? "questions-group" : "subjects-group"
    );

    // Set the heights if all elements are successfully retrieved
    if (group && elements.every((el) => el)) {
      setSubjectsHeights([
        group.clientHeight,
        ...elements.map((el) => el?.clientHeight || 0),
      ]);
    }
  }, [isPatientPage]);

  useEffect(() => {
    if (subjectsHeights[0] > 0) {
      // Calculate the height percentages relative to the main group height
      const percentages = subjectsHeights
        .slice(1)
        .map((height) => (height / subjectsHeights[0]) * 100);

      const handleScroll = () => {
        const group = document.getElementById(
          isPatientPage ? "questions-group" : "subjects-group"
        );

        if (group) {
          const groupRect = group.getBoundingClientRect();
          const groupTop =
            groupRect.top - (isSmallScreen ? 100 : 120) + window.scrollY;
          const groupBottom = groupTop + group.clientHeight;
          const scrollPosition = window.scrollY;

          // Calculate scroll progress within the group element
          const scrollProgress = Math.min(
            Math.max(
              ((scrollPosition - groupTop) / (groupBottom - groupTop)) * 100,
              0
            ),
            100
          );

          // Calculate the dynamic scale for each element based on scroll progress
          const newScales = percentages.map((percentage, index) => {
            const accumulatedPercentage = percentages
              .slice(0, index)
              .reduce((a, b) => a + b, 0);
            const currentScale =
              (scrollProgress - accumulatedPercentage) / percentage;

            return Math.max(0, Math.min(currentScale, 1));
          });

          setDynamicScales(newScales);
        }
      };

      window.addEventListener("scroll", handleScroll);

      return () => window.removeEventListener("scroll", handleScroll);
    }
  }, [subjectsHeights, isPatientPage]);

  return (
    <div className={cx(s.timeline, scrolled ? s.slideUp : s.slideDown)}>
      <button
        className={cx(s.timeline__expand, "timeline-expanded-button")}
        onClick={() => {
          const html = document.querySelector("html");
          html?.classList.toggle("timeline-expanded");
        }}
      >
        <Icon icon="expand-right-line" size={16} />
      </button>
      <ul className={cx(s.timeline__items)}>
        {stints.map((stint, index) => (
          <li key={stint.id}>
            <button
              className={cx(s.stint__item, "body-button")}
              onClick={() => {
                const html = document.querySelector("html");
                html?.classList.remove("timeline-expanded");
                const el = document.getElementById(stint.id);
                if (el) {
                  const yOffset =
                    window.scrollY +
                    el.getBoundingClientRect().top -
                    (isSmallScreen ? 100 : 120);
                  window.scrollTo({ top: yOffset, behavior: "smooth" });
                }
              }}
            >
              <div className={s.stint__inner}>
                <div className={s.stint__dot}>
                  <span />
                </div>
                <div className={cx(s.stint__label, "stint-label")}>
                  {stint.label}
                </div>
              </div>
            </button>
          </li>
        ))}
        <div className={s.timeline__dynamic_lines}>
          {dynamicScales.map((scale, index) => (
            <motion.div
              key={stints[index].id}
              className={cx(
                s.timeline__dynamic_line,
                s[`timeline__dynamic_${index}`]
              )}
              style={{ scaleY: scale }}
            />
          ))}
        </div>
      </ul>
      <button
        className={cx(s.timeline__right, "timeline-right")}
        onClick={() => {
          const html = document.querySelector("html");
          html?.classList.remove("timeline-expanded");
        }}
      ></button>
    </div>
  );
}
